<template>
  <transition name="modal" @click="$emit('close')">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container max-width">
          <div class="modal-header">
            <p class="title-modal">Добавление пользователя</p>
            <div class="modal-header__cross" @click="$emit('close')">
              <img src="../../assets/svg/CrossModal.svg" alt="cross" />
            </div>
          </div>
          <div class="error" v-if="isSubmitted && this.$v.$invalid">
            <p class="error-text">*Заполните все пустые поля</p>
          </div>
          <div class="error" v-if="isSubmitted && this.isCheckPassword">
            <p class="error-text">**Пароли не совпадают</p>
          </div>
          <div class="error-messages">
            <ValidationErrors
              v-if="validationErrors"
              :validation-errors="validationErrors"
            />
          </div>

          <div class="modal-body">
            <div class="modal-body__wrap-input">
              <label for="lastname" class="label-for-input">Фамилия</label>
              <input
                type="text"
                name="lastname"
                v-model="lastname"
                placeholder="Введите фамилию"
                class="input-text"
                :class="{
                  'error-border-input': isSubmitted && $v.lastname.$error,
                }"
              />
              <img
                src="~@/assets/svg/error.svg"
                alt="error"
                v-if="isSubmitted && $v.lastname.$error"
                class="wrapValidationInput__error"
              />
            </div>
            <div class="modal-body__wrap-input">
              <label for="name" class="label-for-input">Имя</label>
              <input
                type="text"
                name="name"
                placeholder="Введите имя"
                v-model="name"
                class="input-text"
                :class="{
                  'error-border-input': isSubmitted && $v.name.$error,
                }"
              />
              <img
                src="~@/assets/svg/error.svg"
                alt="error"
                v-if="isSubmitted && $v.name.$error"
                class="wrapValidationInput__error"
              />
            </div>

            <div class="modal-body__wrap-input">
              <label for="surname" class="label-for-input">Отчество</label>
              <input
                type="text"
                name="surname"
                v-model="surname"
                placeholder="Введите отчество"
                class="input-text"
                :class="{
                  'error-border-input': isSubmitted && $v.surname.$error,
                }"
              />
              <img
                src="~@/assets/svg/error.svg"
                alt="error"
                v-if="isSubmitted && $v.surname.$error"
                class="wrapValidationInput__error"
              />
            </div>
            <div class="modal-body__wrap-input">
              <label for="email" class="label-for-input">Email</label>
              <input
                type="text"
                name="email"
                v-model="email"
                placeholder="Введите email"
                class="input-text"
                :class="{
                  'error-border-input': isSubmitted && $v.email.$error,
                }"
              />
              <img
                src="~@/assets/svg/error.svg"
                alt="error"
                v-if="isSubmitted && $v.email.$error"
                class="wrapValidationInput__error"
              />
            </div>
            <div class="modal-body__wrap-input">
              <label for="phone" class="label-for-input">Телефон</label>
              <the-mask
                :mask="['+#(###)-###-##-##']"
                class="input-number"
                ref="phoneNumber"
                placeholder="+7-(***)-(***)-****"
                v-model="phone"
              />
            </div>
            <div class="modal-body__wrap-input">
              <label for="role" class="label-for-input">Назначить роль</label>
              <select
                name="role"
                id=""
                class="select"
                v-model="role"
                :class="{
                  'error-border-input': isSubmitted && $v.role.$error,
                }"
              >
                <option value="TESTER">Тестирование</option>
                <option value="FIRMWARE">Прошивка</option>
                <option value="STICKERS">Печать</option>
                <option value="ADMIN">Администратор</option>
              </select>
            </div>

            <div class="modal-body__wrap-input">
              <label for="phone" class="label-for-input"
                >Придумайте пароль</label
              >
              <input
                type="password"
                name="password"
                v-model="password"
                placeholder="Придумайте пароль"
                class="input-text"
                :class="{
                  'error-border-input': isSubmitted && $v.password.$error,
                }"
              />
              <img
                src="~@/assets/svg/error.svg"
                alt="error"
                v-if="isSubmitted && $v.password.$error"
                class="wrapValidationInput__error"
              />
            </div>
            <div class="modal-body__wrap-input">
              <label for="phone" class="label-for-input"
                >Повторите пароль</label
              >
              <input
                type="password"
                name="repiatPassword"
                v-model="repiatPassword"
                placeholder="Повторите пароль"
                class="input-text"
                :class="{
                  'error-border-input': isSubmitted && $v.repiatPassword.$error,
                }"
              />
              <img
                src="~@/assets/svg/error.svg"
                alt="error"
                v-if="isSubmitted && $v.repiatPassword.$error"
                class="wrapValidationInput__error"
              />
            </div>
          </div>
          <div class="modal-footer">
            <div class="modal-footer__btn">
              <button
                type="button"
                class="index-btn lite-color"
                @click="$emit('close')"
              >
                Отменить
              </button>
            </div>
            <div class="modal-footer__btn">
              <button
                type="button"
                class="index-btn index-color"
                @click="onSubmitUserInfo"
              >
                Сохранить
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { TheMask } from "vue-the-mask";
import { required } from "vuelidate/lib/validators";
import { actionTypes } from "@/store/modules/users";
import { mapState } from "vuex";
import ValidationErrors from "../ValidationErrors.vue";
export default {
  name: "AppModalAddUser",
  data() {
    return {
      name: "",
      phone: "",
      surname: "",
      lastname: "",
      email: "",
      role: "",
      isSubmitted: false,
      password: "",
      repiatPassword: "",
      isCheckPass: false,
    };
  },
  validations: {
    name: {
      required,
    },
    surname: {
      required,
    },
    lastname: {
      required,
    },

    email: {
      required,
    },
    role: {
      required,
    },
    password: {
      required,
    },
    repiatPassword: {
      required,
    },
  },
  components: { TheMask, ValidationErrors },
  props: {},
  computed: {
    ...mapState({
      validationErrors: (state) => state.users.validationErrors,
    }),
    isCheckPassword() {
      if (
        this.password !== this.repiatPassword ||
        (this.password === "" && this.repiatPassword === "")
      ) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    onSubmitUserInfo() {
      this.isSubmitted = true;

      this.$v.$touch();
      if (this.$v.$invalid) {
        return console.log("не ок");
      } else {
        this.$store
          .dispatch(actionTypes.addUser, {
            name: this.name,
            lastName: this.lastname,
            surname: this.surname,
            email: this.email,
            role: this.role,
            contactPhone: this.phone,
            password: this.password,
          })
          .then(() => {
            this.$store
              .dispatch(actionTypes.getUsers)
              .then(() => {})
              .catch((er) => {
                console.log("Ошибка", er);
              });
            this.$emit("close");
          })
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
  },
};
</script>

<style></style>
