<template>
  <div class="pie">
    <div class="pie__labels">
      <div
        class="pie__labels-block"
        v-for="(label, index) in this.chartData.labels"
        :key="index"
      >
        <p
          class="index-text400 number"
          :class="{ 'number-rang-active ': index < 3 }"
        >
          {{ index + 1 }}.
        </p>
        <p
          class="pie__label-item index-text400 number"
          :class="{ 'rang-active': index < 3 }"
        >
          {{ label }}
        </p>
      </div>
    </div>
    <Pie
      :chart-data="chartData"
      :chartOptions="chartOptions"
      style="width: 40%"
    />
  </div>
</template>

<script>
import { Pie } from "vue-chartjs";
import {
  Chart,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
} from "chart.js";
import { mapState } from "vuex";
Chart.register(Title, Tooltip, Legend, ArcElement, CategoryScale);

export default {
  name: "PieChart",
  components: { Pie },
  computed: {
    ...mapState({
      pieStatFirm: (state) => state.firmware.pieStatFirm,
      loading: (state) => state.firmware.loading,
    }),
    chartData() {
      return {
        labels: this.pieStatFirm.map(
          (c) => `${c.username} - ${c.devices_made}`
        ),
        datasets: [
          {
            label: "Пройдено ",
            backgroundColor: [
              "rgba(25, 44, 140, 0.8)",
              "rgba(15, 49, 169, 0.8)",
              "rgba(15, 40, 169, 0.5)",
              "rgba(147, 170, 252, 0.8)",
              "rgba(198, 210, 253, 0.8)",
              "rgba(229, 234, 252, 0.8)",
              "rgba(201, 203, 207, 0.2)",
              "rgba(15, 40, 169, 0.5)",
              "rgba(147, 170, 252, 0.8)",
              "rgba(198, 210, 253, 0.8)",
              "rgba(229, 234, 252, 0.8)",
              "rgba(201, 203, 207, 0.2)",
            ],

            data: this.pieStatFirm.map((c) => c.devices_made),
          },
        ],
      };
    },
  },
  data() {
    return {
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false, // отключает легенду, бесполезную когда на графике одна линия,
          position: "top",
          align: "start",
          padding: 20,
        },
        //   options: {
        plugins: {
          legend: {
            display: false,
            // position: "top",
            // align: "start",

            // labels: {
            //   padding: 15,
            //   color: "#000",
            //   font: '"Raleway", sans-serif',
            //   usePointStyle: true,
            //   pointStyleWidth: 17,
            // },
          },
        },
        //   },
      },
    };
  },
};
</script>
