import axios from "@/api/axios";
const getAllDevice = () => {
  return axios.get(`/all_device`);
};
const searchIMEI = (imei) => {
  return axios.get(`search_device_imei/${imei}`);
};
const searchCCID = (ccid) => {
  return axios.get(`search_device_ccid/${ccid}`);
};
const getAllTestForDevice = (imei) => {
  return axios.get(`/getAllInfoAboutDevice?imei=${imei}`);
};

const getDateReportDevice = (date) => {
  return axios.post(`/device/get_data_to_date`, date);
};
const downloadFileDevice = (data) => {
  return axios.post(`/device/export_excel`, data, {
    responseType: "blob",
  });
};
const downloadZipDevice = (data) => {
  return axios.post(`/device/download/report`, data, {
    responseType: "blob",
  });
};
const downloadPdfDeviceItem =(imei) => {
  return axios.post(`/device/download/pdf`, imei, {
    responseType: "blob",
  });
}

const getDataStatDevice = (date) => {
  return axios.post(`/device/get_stat`, date);
};
export default {
  getAllDevice,
  searchIMEI,
  searchCCID,
  getAllTestForDevice,
  getDateReportDevice,
  downloadFileDevice,
  getDataStatDevice,
  downloadZipDevice,
  downloadPdfDeviceItem
};
