<template>
  <div class="testing__statistics-container-wrap">
    <div class="statistics-bar-report__data-tab">
      <div class="statistics-bar-report__title">
        <p class="statistics-bar__title-text">Фильтрация</p>
      </div>
      <div class="statistics-bar-report__fillter">
        <div class="statistics-bar-report__btn">
          <div
            class="data-tab__btn-with-border"
            :class="{ 'active-btn': this.isActiveToday }"
            @click="changeChart(nowDate, nowDate), activeBlock('today')"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveToday }"
            >
              Сегодня
            </p>
          </div>
          <div
            class="data-tab__btn-with-border"
            :class="{ 'active-btn': this.isActiveWeek }"
            @click="changeChart(nowDate, beforeWeekDate), activeBlock('week')"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveWeek }"
            >
              За 7 дней
            </p>
          </div>
          <div
            class="data-tab__btn-with-border"
            :class="{ 'active-btn': this.isActiveMounth }"
            @click="changeChart(nowDate, beforeMounth), activeBlock('mounth')"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveMounth }"
            >
              За 30 дней
            </p>
          </div>
        </div>
        <div class="statistics-bar-report__block">
          <VueDatePicker
            v-model="dateForTest"
            range
            validate
            :color="this.color"
            :class="{ 'active-btn': this.isActiveSelect }"
            placeholder="Выберите диапазон дат"
            @onChange="getDataTest(), activeBlock('select')"
            class="number custom__button"
          >
            <template #day="{ day }" class="number">
              <span class="number custom__button">
                {{ day }}
              </span>
            </template>
          </VueDatePicker>
        </div>
      </div>
    </div>
    <div class="statistics__wrap">
      <div class="statistics-avg">
        <div class="statistics-bar__title">
          <p class="statistics-bar__description">статистика</p>
          <p class="statistics-bar__title-text">
            Общие показатели за указанный период времени
          </p>
        </div>
        <div class="statistics-bar-report__date-avg"></div>
        <testing-stat-avg />
      </div>
      <div class="statistics__wrap-diagram">
        <div class="statistics-bar">
          <div class="statistics-bar__wrap">
            <div class="statistics-bar__title">
              <p class="statistics-bar__description">статистика</p>
              <p class="statistics-bar__title-text">
                Количество протестированных устройств
              </p>
            </div>

            <div class="statistics-bar-report__block-wrap">
              <div class="statistics-bar-report__date"></div>
            </div>
          </div>
          <testing-bar />
        </div>
        <div class="statistics-pie">
          <div class="statistics-bar__title">
            <p class="statistics-bar__description">статистика</p>
            <p class="statistics-bar__title-text">
              Диаграмма количества протестированных блоков сотрудниками
            </p>
          </div>
          <testing-pie />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { VueDatePicker } from "@mathieustan/vue-datepicker";
import TestingBar from "../Statistics/TestingBar.vue";
import TestingPie from "../Statistics/TestingPie.vue";
import { actionTypes } from "@/store/modules/testing";
import TestingStatAvg from "./TestingStatAvg.vue";

export default {
  name: "AppTestingStatistics",
  data() {
    return {
      dateForTest: "",
      color: "#192c8c",
      dateForAvg: "",
      isActiveToday: false,
      isActiveWeek: true,
      isActiveMounth: false,
      isActiveSelect: false,
    };
  },
  components: {
    VueDatePicker,
    TestingBar,
    TestingStatAvg,
    TestingPie,
  },
  computed: {
    isComplete() {
      return this.dateForTest != "";
    },

    nowDate() {
      var date = new Date();

      return date.toISOString().substring(0, 10);
    },
    beforeWeekDate() {
      var date = new Date();
      return new Date(date.setDate(date.getDate() - 7))
        .toISOString()
        .substring(0, 10);
    },
    beforeMounth() {
      var date = new Date();
      return new Date(date.setDate(date.getDate() - 30))
        .toISOString()
        .substring(0, 10);
    },
  },
  methods: {
    //TAB
    activeBlock(tab) {
      if (tab === "today") {
        this.isActiveToday = true;
        this.isActiveWeek = false;
        this.isActiveMounth = false;
        this.isActiveSelect = false;
      } else if (tab === "week") {
        this.isActiveToday = false;
        this.isActiveWeek = true;
        this.isActiveMounth = false;
        this.isActiveSelect = false;
      } else if (tab === "mounth") {
        this.isActiveToday = false;
        this.isActiveWeek = false;
        this.isActiveMounth = true;
        this.isActiveSelect = false;
      } else if (tab === "select") {
        this.isActiveToday = false;
        this.isActiveWeek = false;
        this.isActiveMounth = false;
        this.isActiveSelect = true;
      }
    },
    // changeDataChart() {},
    getDataTest() {
      this.$store
        .dispatch(actionTypes.getDataStatTest, this.dateForTest)
        .then(() => {})
        .catch((er) => {
          console.log("Ошибка", er);
        });
      this.$store
        .dispatch(actionTypes.getAvgData, this.dateForTest)
        .then(() => {})
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
    changeChart(end, start) {
      this.$store
        .dispatch(actionTypes.getDataStatTest, {
          end: end,
          start: start,
        })
        .then(() => {})
        .catch((er) => {
          console.log("Ошибка", er);
        });

      this.$store
        .dispatch(actionTypes.getAvgData, {
          end: end,
          start: start,
        })
        .then(() => {})
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
  },
  mounted() {
    //Начальные данные за неделю
    this.$store
      .dispatch(actionTypes.getDataStatTest, {
        end: this.nowDate,
        start: this.beforeWeekDate,
      })
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
    this.$store
      .dispatch(actionTypes.getAvgData, {
        end: this.nowDate,
        start: this.beforeWeekDate,
      })
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>
<style lang="scss" scoped>
.custom__button {
}
</style>
