<template>
  <div class="users">
    <Header
      title="Пользователи"
      description="Добавление и отслеживание активности"
    />
    <div class="wrap-loader-2" v-if="isLoading">
      <div id="loader"></div>
    </div>
    <div class="container-page">
      <div class="users__block">
        <div class="users__search">
          <input
            type="text"
            placeholder="Поиск по ФИО"
            class="testing__input"
            v-model="searchFIO"
          />
          <img
            src="../assets/svg/search.svg"
            alt="search"
            class="testing__input-svg"
          />
        </div>
        <div class="users__wrap">
          <div class="users__add">
            <button
              type="button"
              class="index-btn index-color"
              @click="openModal"
            >
              <img
                src="../assets/svg/plus.svg"
                alt="plus"
                class="index-btn__img"
              />
              Добавить пользователя
            </button>
          </div>
        </div>
      </div>
      <div class="users__info">
        <div class="card-users">
          <ItemCardUser
            v-for="user in this.paginatedData"
            :key="user.id"
            :item_user="user"
            @editItem="editItem"
          />
          <template>
            <div class="pagination">
              <button
                :disabled="pageNumber === 0"
                @click="prevPage()"
                class="pagination__btn"
              >
                <arrow-left :color="this.colorArrowLeft" />
              </button>

              <li
                v-for="(item, index) in this.totalPages"
                class="pagination-item"
                :key="index"
              >
                <button
                  type="button"
                  @click="onClickPage(item)"
                  class="pagination-item__btn number"
                  :class="{ 'active-num': pageNumber === index }"
                >
                  {{ item }}
                </button>
              </li>
              <button
                :disabled="pageNumber >= pageCount - 1"
                @click="nextPage()"
                class="pagination__btn"
              >
                <arrow-right :color="this.colorArrowRight" />
              </button>
            </div>
          </template>
        </div>
        <div class="statistics-user">
          <StatisticsBlockUser />
        </div>
      </div>
    </div>

    <ModalAddUser
      v-if="isOpenModalAddUser"
      @close="isOpenModalAddUser = false"
    />
    <ModalEditUser
      v-if="isOpenModalEditUser"
      @close="isOpenModalEditUser = false"
      :item_user="this.item_user"
    />
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import ModalAddUser from "../components/Modal/ModalAddUser.vue";
import ItemCardUser from "../components/UsersBlock/ItemCardUser.vue";
import StatisticsBlockUser from "../components/UsersBlock/StatisticsBlockUser.vue";
import { actionTypes } from "@/store/modules/users";
import { mapState } from "vuex";
import ModalEditUser from "../components/Modal/ModalEditUser.vue";
import ArrowLeft from "../components/Svg/Arrow-Left.vue";
import ArrowRight from "../components/Svg/Arrow-Right.vue";
// import { actionTypesSocket } from "@/store/modules/socket";

export default {
  components: {
    Header,
    ItemCardUser,
    StatisticsBlockUser,
    ModalAddUser,
    ModalEditUser,
    ArrowLeft,
    ArrowRight,
  },
  name: "AppUsers",
  data() {
    return {
      isOpenModalAddUser: false,
      isOpenModalEditUser: false,
      item_user: "",
      currentPage: 1,
      pageNumber: 0,
      title: "Пользователи",
      searchFIO: "",
    };
  },
  props: {
    //число объектов на странице
    size: {
      type: Number,
      required: false,
      default: 7,
    },
    // максимальное количество видимых кнопок
    maxVisibleButtons: {
      type: Number,
      required: false,
      default: 5,
    },
  },
  methods: {
    addUser() {
      console.log();
    },
    openModal() {
      this.isOpenModalAddUser = !this.isOpenModalAddUser;
    },
    editItem(item) {
      this.isOpenModalEditUser = !this.isOpenModalEditUser;
      this.item_user = item;
    },

    //пагинаця
    onClickPage(page) {
      this.pageNumber = page - 1;
    },
    //следующая страница
    nextPage() {
      this.pageNumber++;
    },
    //предыдущая
    prevPage() {
      this.pageNumber--;
    },
  },
  computed: {
    ...mapState({
      allUsers: (state) => state.users.allUsers,
      isLoading: (state) => state.users.isLoading,
      token: (state) => state.auth.token,
      infoUser: (state) => state.auth.infoUser,
      isConnected: (state) => state.socket.isConnected,
    }),
    //количество страниц
    totalPages() {
      if (this.allUsers !== []) {
        return Math.ceil(this.allUsers.length / this.size);
      } else {
        return 0;
      }
    },
    searchFIOReact() {
      return this.searchFIO;
    },
    //текущая страница

    //пагинация
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      return this.allUsers.slice(start, end);
    },
    pageCount() {
      if (this.allUsers !== []) {
        let l = this.allUsers.length,
          s = this.size;
        return Math.ceil(l / s);
      } else {
        return 0;
      }
    },
    //
    colorArrowLeft() {
      if (this.pageNumber === 0) {
        return "#7C8DB5";
      } else {
        return "#192c8c";
      }
    },
    colorArrowRight() {
      if (this.pageNumber >= this.pageCount - 1) {
        return "#7C8DB5";
      } else {
        return "#192c8c";
      }
    },
  },
  watch: {
    searchFIOReact() {
      let fio = this.searchFIOReact;

      if (fio !== "") {
        this.$store
          .dispatch(actionTypes.getSearchFio, fio)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      } else {
        this.$store
          .dispatch(actionTypes.getUsers)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
  },
  beforeMount() {
    document.title = this.title;
  },
  beforeUpdate() {
    document.title = this.title;
  },
  mounted() {
    if (
      this.token === "undefined" ||
      this.token === "" ||
      this.infoUser === null
    ) {
      this.$router.push({ name: "Login" });
    }
    // if (this.infoUser.id && !this.isConnected) {
    //   this.$store.dispatch(actionTypesSocket.socketConnect, this.infoUser.id);
    // }
    this.$store
      .dispatch(actionTypes.getUsers)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>
