import axios from "@/api/axios";
const getAllTests = () => {
  return axios.get(`/all_test`);
};
const getSearch = (imei) => {
  return axios.get(`/test_search/${imei}`);
};
const getDataStatTest = (date) => {
  return axios.post(`/test/get_stat`, date);
};

const getDateReport = (date) => {
  return axios.post(`/test/get_data_to_date`, date);
};
const downloadFile = (data) => {
  return axios.post(`/test/export_test`, data, {
    responseType: "blob",
  });
};
const getAvgData = (data) => {
  return axios.post(`/test/get_avg_data`, data);
};

export default {
  getAllTests,
  getSearch,
  getDataStatTest,
  getDateReport,
  downloadFile,
  getAvgData,
};
