import firmwaregApi from "@/api/firmware.js";

const state = {
  isLoading: false, //ожидание запроса
  validationErrors: null, //ошибки
  allFirmware: [],
  dataBarStatFirm: [],
  dataReportFirm: [],
  avgStatFirm: [],
  pieStatFirm: [],
};

export const mutationTypes = {
  getAllFirmwareStart: "[firmware] getAllFirmwareStart",
  getAllFirmwareSuccess: "[firmware] getAllFirmwareSuccess",
  getAllFirmwareFailure: "[firmware] getAllFirmwareFailure",

  getSearchIMEIStart: "[firmware] getSearchIMEIStart",
  getSearchIMEISuccess: "[firmware] getSearchIMEISuccess",
  getSearchIMEIFailure: "[firmware] getSearchIMEIFailure",

  getDataStatFirmStart: "[firmware] getDataStatFirmStart",
  getDataStatFirmSuccess: "[firmware] getDataStatFirmSuccess",
  getDataStatFirmFailure: "[firmware] getDataStatFirmFailure",

  getDateReportFirmStart: "[firmware] getDateReportFirmStart",
  getDateReportFirmSuccess: "[firmware] getDateReportFirmSuccess",
  getDateReportFirmFailure: "[firmware] getDateReportFirmFailure",

  downloadFileFirmStart: "[firmware]  downloadFileFirmStart",
  downloadFileFirmSuccess: "[firmware]  downloadFileFirmSuccess",
  downloadFileFirmFailure: "[firmware]  downloadFileFirmFailure",

  getAvgDataFirmStart: "[firmware] getAvgDataFirmStart",
  getAvgDataFirmSuccess: "[firmware] getAvgDataFirmSuccess",
  getAvgDataFirmFailure: "[firmware] getAvgDataFirmFailure",
};
export const actionTypes = {
  getAllFirmware: "[firmware] getAllFirmware",
  getSearchIMEI: "[firmware] getSearchIMEI",
  getDataStatFirm: "[firmware]  getDataStatFirm",
  getDateReportFirm: "[firmware] getDateReportFirm",
  downloadFileFirm: "[firmware] downloadFileFirm",
  getAvgDataFirm: "[firmware] getAvgDataFirm",
};

const mutations = {
  [mutationTypes.getAllFirmwareStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getAllFirmwareSuccess](state, data) {
    state.isLoading = false;

    state.allFirmware = data.result;
  },
  [mutationTypes.getAllFirmwareFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  [mutationTypes.getSearchIMEIStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getSearchIMEISuccess](state, data) {
    state.isLoading = false;
    state.allFirmware = data.result;
  },
  [mutationTypes.getSearchIMEIFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //Отображение временной статистики(по датам)
  [mutationTypes.getDataStatFirmStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getDataStatFirmSuccess](state, data) {
    state.isLoading = false;
    state.dataBarStatFirm = data.result;
  },
  [mutationTypes.getDataStatFirmFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //получение данных для отчета по прошивке блоков

  [mutationTypes.getDateReportFirmStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getDateReportFirmSuccess](state, data) {
    state.isLoading = false;
    state.dataReportFirm = data.result;
  },
  [mutationTypes.getDateReportFirmFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //скачивание докуменда
  [mutationTypes.downloadFileFirmStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.downloadFileFirmSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.downloadFileFirmFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //получение всех средних по статистике прошивки
  [mutationTypes.getAvgDataFirmStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getAvgDataFirmSuccess](state, payload) {
    state.isLoading = false;
    state.avgStatFirm = payload.result;
    state.pieStatFirm = payload.result.madeDevices;
  },
  [mutationTypes.getAvgDataFirmFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
};
const actions = {
  [actionTypes.getAllFirmware](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getAllFirmwareStart);
      firmwaregApi
        .getAllFirmware()
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getAllFirmwareSuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getAllFirmwareFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getAllFirmwareFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  //
  [actionTypes.getSearchIMEI](context, imei) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getSearchIMEIStart);
      firmwaregApi
        .getSearchIMEI(imei)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getSearchIMEISuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getSearchIMEIFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getSearchIMEIFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getDataStatFirm](context, date) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getDataStatFirmStart);
      firmwaregApi
        .getDataStatFirm(date)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getDataStatFirmSuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getDataStatFirmFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getDataStatFirmFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getDateReportFirm](context, date) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getDateReportFirmStart);
      firmwaregApi
        .getDateReportFirm(date)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(
              mutationTypes.getDateReportFirmSuccess,
              response.data
            );
            resolve(response.data);
          } else {
            context.commit(
              mutationTypes.getDateReportFirmFailure,
              response.data
            );
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getDateReportFirmFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.downloadFileFirm](context, data) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.downloadFileFirmStart);
      firmwaregApi
        .downloadFileFirm(data)
        .then((response) => {
          context.commit(mutationTypes.downloadFileFirmSuccess, response);
          resolve(response);
        })
        .catch((err) => {
          context.commit(mutationTypes.downloadFileFirmFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getAvgDataFirm](context, data) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getAvgDataFirmStart);
      firmwaregApi
        .getAvgDataFirm(data)
        .then((response) => {
          context.commit(mutationTypes.getAvgDataFirmSuccess, response.data);
          resolve(response);
        })
        .catch((err) => {
          context.commit(mutationTypes.getAvgDataFirmFailure, err);
          console.log("ошибка", err);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
