import Vue from "vue";
import VueRouter from "vue-router";
import Users from "../views/Users.vue";
import Login from "../views/Login.vue";
import Testing from "../views/Testing.vue";
import Firmware from "../views/Firmware.vue";
import Profile from "../views/Profile.vue";
import Device from "../views/Device.vue";
import InformationIMEI from "../views/InformationIMEI";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Users",
    component: Users,
    meta: {
      Sidebar: true,
    },
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    meta: {
      Sidebar: false,
    },
  },
  {
    path: "/testing",
    name: "Testing",
    component: Testing,
    meta: {
      Sidebar: true,
    },
  },
  {
    path: "/information/:imei",
    name: "InformationIMEI",
    component: InformationIMEI,
    meta: {
      Sidebar: true,
    },
  },
  {
    path: "/firmware",
    name: "Firmware",
    component: Firmware,
    meta: {
      Sidebar: true,
    },
  },
  {
    path: "/device",
    name: "Device",
    component: Device,
    meta: {
      Sidebar: true,
    },
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile,
    meta: {
      Sidebar: true,
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
