import testingApi from "@/api/testing.js";

const state = {
  isLoading: false, //ожидание запроса
  validationErrors: null, //ошибки
  allTests: [],
  searchIMEI: [],
  dataBarStat: [],
  dataReport: [],
  avgStat: [],
  pieStatTest: [],
};

export const mutationTypes = {
  getAllTestsStart: "[testing] getAllTestsStart",
  getAllTestsSuccess: "[testing] getAllTestsSuccess",
  getAllTestsFailure: "[testing] getAllTestsFailure",

  getSearchStart: "[testing] getSearchStart",
  getSearchSuccess: "[testing] getSearchSuccess",
  getSearchFailure: "[testing] getSearchFailure",

  getDataStatTestStart: "[testing] getDataStatTestStart",
  getDataStatTestSuccess: "[testing] getDataStatTestSuccess",
  getDataStatTestFailure: "[testing] getDataStatTestFailure",

  getDateReportStart: "[testing] getDateReportStart",
  getDateReportSuccess: "[testing] getDateReportSuccess",
  getDateReportFailure: "[testing] getDateReportFailure",

  downloadFileStart: "[testing]  downloadFileStart",
  downloadFileSuccess: "[testing]  downloadFileSuccess",
  downloadFileFailure: "[testing]  downloadFileFailure",

  getAvgDataStart: "[testing] getAvgDataStart",
  getAvgDataSuccess: "[testing] getAvgDataSuccess",
  getAvgDataFailure: "[testing] getAvgDataFailure",
};
export const actionTypes = {
  getAllTests: "[testing] getAllTests",
  getSearch: "[testing] getSearch",
  getDataStatTest: "[testing] getDataStatTest",
  getDateReport: "[testing] getDateReport",
  downloadFile: "[testing]  downloadFile",
  getAvgData: "[testing] getAvgData",
};

const mutations = {
  [mutationTypes.getAllTestsStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getAllTestsSuccess](state, data) {
    console.log(data);
    state.isLoading = false;
    state.allTests = data.result;
  },
  [mutationTypes.getAllTestsFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //ПОИСК IMEI
  [mutationTypes.getSearchStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getSearchSuccess](state, data) {
    state.isLoading = false;
    // state.searchIMEI = data.result;
    state.allTests = data.result;
  },
  [mutationTypes.getSearchFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //Отображение временной статистики(по датам) getDataStatTest
  [mutationTypes.getDataStatTestStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getDataStatTestSuccess](state, data) {
    state.isLoading = false;
    state.dataBarStat = data.result;
  },
  [mutationTypes.getDataStatTestFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //получение данных для отчета по тестированию блоков

  [mutationTypes.getDateReportStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getDateReportSuccess](state, data) {
    state.isLoading = false;
    state.dataReport = data.result;
  },
  [mutationTypes.getDateReportFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //скачивание докуменда
  [mutationTypes.downloadFileStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.downloadFileSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.downloadFileFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
  //получение всех средних по статистике тестирования
  [mutationTypes.getAvgDataStart](state) {
    state.validationErrors = null;
    state.isLoading = true;
  },
  [mutationTypes.getAvgDataSuccess](state, payload) {
    state.isLoading = false;
    state.avgStat = payload.result;
    state.pieStatTest = payload.result.madeDevices;
  },
  [mutationTypes.getAvgDataFailure](state, payload) {
    state.validationErrors = payload.message;
    state.isLoading = false;
  },
};
const actions = {
  [actionTypes.getAllTests](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getAllTestsStart);
      testingApi
        .getAllTests()
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getAllTestsSuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getAllTestsFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getAllTestsFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getSearch](context, num) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getSearchStart);
      testingApi
        .getSearch(num)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getSearchSuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getSearchFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getSearchFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getDataStatTest](context, date) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getDataStatTestStart);
      testingApi
        .getDataStatTest(date)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getDataStatTestSuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getDataStatTestFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getDataStatTestFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getDateReport](context, date) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getDateReportStart);
      testingApi
        .getDateReport(date)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            context.commit(mutationTypes.getDateReportSuccess, response.data);
            resolve(response.data);
          } else {
            context.commit(mutationTypes.getDateReportFailure, response.data);
          }
        })
        .catch((err) => {
          context.commit(mutationTypes.getDateReportFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.downloadFile](context, data) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.downloadFileStart);
      testingApi
        .downloadFile(data)
        .then((response) => {
          context.commit(mutationTypes.downloadFileSuccess, response);
          resolve(response);
        })
        .catch((err) => {
          context.commit(mutationTypes.downloadFileFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  [actionTypes.getAvgData](context, data) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getAvgDataStart);
      testingApi
        .getAvgData(data)
        .then((response) => {
          context.commit(mutationTypes.getAvgDataSuccess, response.data);
          resolve(response);
        })
        .catch((err) => {
          context.commit(mutationTypes.getAvgDataFailure, err);
          console.log("ошибка", err);
        });
    });
  },
  //
};
export default {
  state,
  mutations,
  actions,
};
