import axios from "@/api/axios";
const addUser = (credentials) => {
  return axios.post(`/registration`, credentials);
};
const getUsers = () => {
  return axios.get(`/users`);
};
const deleteUser = (userId) => {
  return axios.post(`/delete/user/${userId}`);
};
const editUser = (data) => {
  return axios.post(`/edit/user`, data);
};
const getSearchFio = (fio) => {
  return axios.get(`/search/fio/${fio}`);
};

export default {
  addUser,
  getUsers,
  deleteUser,
  editUser,
  getSearchFio,
};
