<template>
  <ul class="pagination">
    <li class="pagination-item">
      <button
        type="button"
        @click="onClickFirstPage"
        :disabled="isInFirstPage"
        aria-label="Go to first page"
        class="pagination-item__text number"
      >
        Первая
      </button>
    </li>

    <li class="pagination-item">
      <button
        type="button"
        @click="onClickPreviousPage"
        :disabled="isInFirstPage"
        class="pagination__btn"
        aria-label="Go to previous page"
      >
        <arrow-left :color="this.colorArrowLeft" />
      </button>
    </li>

    <li v-for="(page, index) in pages" class="pagination-item" :key="index">
      <button
        type="button"
        @click="onClickPage(page.name)"
        :disabled="page.isDisabled"
        :class="{ 'active-num': isPageActive(page.name) }"
        :aria-label="`Go to page number ${page.name}`"
        class="pagination-item__btn number"
      >
        {{ page.name }}
      </button>
    </li>

    <li class="pagination-item">
      <button
        type="button"
        @click="onClickNextPage"
        :disabled="isInLastPage"
        aria-label="Go to next page"
        class="pagination__btn"
      >
        <arrow-right :color="this.colorArrowRight" />
      </button>
    </li>

    <li class="pagination-item">
      <button
        type="button"
        @click="onClickLastPage"
        :disabled="isInLastPage"
        aria-label="Go to last page"
        class="pagination-item__text number"
      >
        Последняя
      </button>
    </li>
  </ul>
</template>

<script>
import ArrowLeft from "../Svg/Arrow-Left.vue";
import ArrowRight from "../Svg/Arrow-Right.vue";
export default {
  components: { ArrowRight, ArrowLeft },
  name: "AppPagination",

  props: {
    maxVisibleButtons: {
      type: Number,
      required: false,
      default: 5,
    },
    totalPages: {
      type: Number,
      required: true,
    },
    pageCount: {
      type: Number,
      required: true,
    },
    //  total: {
    //    type: Number,
    //    required: true,
    //  },
    perPage: {
      type: Number,
      required: true,
    },
    currentPage: {
      type: Number,
      required: true,
    },
  },
  computed: {
    startPage() {
      if (this.currentPage === 1) {
        return 1;
      }

      if (this.currentPage === this.totalPages) {
        return this.totalPages - this.maxVisibleButtons + 1;
      }

      return this.currentPage - 1;
    },
    endPage() {
      return Math.min(
        this.startPage + this.maxVisibleButtons - 1,
        this.totalPages
      );
    },
    pages() {
      const range = [];

      for (let i = this.startPage; i <= this.endPage; i += 1) {
        range.push({
          name: i,
          isDisabled: i === this.currentPage,
        });
      }

      return range;
    },
    isInFirstPage() {
      return this.currentPage === 1;
    },
    isInLastPage() {
      return this.currentPage === this.totalPages;
    },
    colorArrowLeft() {
      if (this.currentPage === 1) {
        return "#7C8DB5";
      } else {
        return "#192c8c";
      }
    },
    colorArrowRight() {
      if (this.currentPage >= this.pageCount - 1) {
        return "#7C8DB5";
      } else {
        return "#192c8c";
      }
    },
  },
  methods: {
    onClickFirstPage() {
      this.$emit("pagechanged", 1);
    },
    onClickPreviousPage() {
      this.$emit("pagechanged", this.currentPage - 1);
    },
    onClickPage(page) {
      this.$emit("pagechanged", page);
    },
    onClickNextPage() {
      this.$emit("pagechanged", this.currentPage + 1);
    },
    onClickLastPage() {
      this.$emit("pagechanged", this.totalPages);
    },
    isPageActive(page) {
      return this.currentPage === page;
    },
  },
};
</script>

<style>
.pagination {
  list-style-type: none;
}

.pagination-item {
  display: inline-block;
  margin: 10px;
}

.active {
  background-color: #4aae9b;
  color: #ffffff;
}
</style>