<template>
  <div class="">
    <div class="report">
      <div class="report__title">Для создания отчета выберите даты</div>
      <div
        class="report__wrap-download"
        @click="downloadFile"
        v-if="isSubmiting && this.dataReportDevice.length"
      >
        <div class="report__download-icon" tooltip="Скачать">
          <img src="../../assets/svg/download.svg" alt="download" />
        </div>
        <div class="report__download-text">Скачать документ</div>
      </div>
      <div class="report__block-wrap">
        <div class="report__block">
          <VueDatePicker
            v-model="date"
            range
            validate
            :color="this.color"
            placeholder="Выберите даты"
            class="number"
          >
            <template #day="{ day }" class="number">
              <span class="number">
                {{ day }}
              </span>
            </template>
          </VueDatePicker>
        </div>
        <div class="report__block-type">
          <label for="role" class="label-for-input">Выберите тип отчета:</label>
          <select name="typeReport" id="" class="select" v-model="typeReport">
            <option value="ALL">Общий отчет</option>
            <option value="RELEASE">EXCEL Отчет по готовым блокам</option>
            <option value="PDF">PDF отчет по результатам испытаний</option>
          </select>
        </div>

        <div class="report__btn">
          <button
            type="button"
            class="index-btn lite-color"
            :disabled="!isComplete"
            @click="createReport"
          >
            Создать отчет
          </button>
        </div>
      </div>
      <div class="device-block__list" v-if="isSubmiting">
        <template v-if="this.dataReportDevice.length">
          <item-device
            v-for="(device, index) in this.dataReportDevice"
            :key="index"
            :device_item="device"
          />
        </template>
        <div class="error" v-else>
          <p class="error-text">Не найдено</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { VueDatePicker } from "@mathieustan/vue-datepicker";
import { actionTypesDevice } from "@/store/modules/device";
import { mapState } from "vuex";
import ItemDevice from "./ItemDevice.vue";

// import FirmwareReportTable from "./FirmwareReportTable.vue";
// import TableDevice from "../Table/TableDevice.vue";
// import Pagination from "../Pagination/Pagination.vue";
// import axios from "axios";
export default {
  name: "AppDeviceActiveReport",
  data() {
    return {
      color: "#192c8c",
      date: "",
      currentPage: 1,
      pageNumber: 0,
      isSubmiting: false,
      typeReport: "",
      imei: "",
    };
  },
  props: {
    //число объектов на странице
    size: {
      type: Number,
      required: false,
      default: 10,
    },
  },
  components: {
    VueDatePicker,
    ItemDevice,

    // FirmwareReportTable,
    //  TableDevice,
    //  Pagination,
  },
  computed: {
    ...mapState({
      dataReportDevice: (state) => state.device.dataReportDevice,
    }),
    isComplete() {
      return this.date != "" && this.typeReport !== "";
    },
    //PAGINATION
    //количество страниц
    totalPages() {
      if (this.dataReport !== []) {
        return Math.ceil(this.dataReportFirm.length / this.size);
      } else {
        return 0;
      }
    },
    //текущая страница

    //пагинация
    paginatedData() {
      const start = (this.currentPage - 1) * this.size,
        end = start + this.size;
      return this.dataReportFirm.slice(start, end);
    },
    pageCount() {
      if (this.dataReportFirm !== []) {
        let l = this.dataReportFirm.length,
          s = this.size;
        return Math.ceil(l / s);
      } else {
        return 0;
      }
    },
  },
  methods: {
    downloadFile() {
      const release = {
        start: this.date.start,
        end: this.date.end,
        type: this.typeReport,
      };
      if( this.typeReport == 'PDF') {
        console.log('КАЧ PDF')
        this.$store
            .dispatch(actionTypesDevice.downloadZipDevice, release)
            .then((response) => {
              console.log(response, 'response ZIP');
              //Создаем ссылку для скачивания
              const url = window.URL.createObjectURL(new Blob([response.data]));
              // Создаем ссылку для скачивания
              const link = document.createElement("a");
              link.href = url;
              var fileName = `${release.start}-${release.end}.zip`;
              link.setAttribute("download", fileName); // Указываем имя файла
              // Симулируем клик по ссылке для скачивания
              document.body.appendChild(link);
              link.click();
              // Освобождаем ресурсы
              window.URL.revokeObjectURL(url);
            })
            .catch((er) => {
              console.log("Ошибка", er);
            });

      } else {
        this.$store
            .dispatch(actionTypesDevice.downloadFileDevice, release)
            .then((response) => {
              const url = URL.createObjectURL(
                  new Blob([response.data], {
                    type: "application/vnd.ms-excel",
                  })
              );
              var fileName = `Отчет по блокам ${this.date.start} - ${this.date.end}`;
              const link = document.createElement("a");
              link.href = url;
              link.setAttribute("download", fileName);
              document.body.appendChild(link);
              link.click();
            })
            .catch((er) => {
              console.log("Ошибка", er);
            });
      }

    },
    createReport() {
      this.$store
        .dispatch(actionTypesDevice.getDateReportDevice, this.date)
        .then(() => {
          this.isSubmiting = true;
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
    //ПАГИНАЦИЯ
    onPageChange(page) {
      this.currentPage = page;
    },
  },
};
</script>

