<template>
  <div class="">
    <div class="report">
      <div class="report__title">Для создания отчета выберите даты</div>
      <div
        class="report__wrap-download"
        @click="downloadFile"
        v-if="isSubmiting"
      >
        <div class="report__download-icon" tooltip="Скачать">
          <img src="../../assets/svg/download.svg" alt="download" />
        </div>
        <div class="report__download-text">Скачать документ</div>
      </div>
      <div class="report__block-wrap">
        <div class="report__block">
          <VueDatePicker
            v-model="date"
            range
            validate
            :color="this.color"
            placeholder="Выберите даты"
            class="number"
          >
            <template #day="{ day }" class="number">
              <span class="number">
                {{ day }}
              </span>
            </template>
          </VueDatePicker>
        </div>
        <div class="report__btn">
          <button
            type="button"
            class="index-btn lite-color"
            :disabled="!isComplete"
            @click="createReport"
          >
            Создать отчет
          </button>
        </div>
      </div>
      <div class="report__table-wrap" v-if="isSubmiting">
        <testing-report-table :pagination_data="this.paginatedData" />

        <template>
          <pagination
            :total-pages="this.totalPages"
            :per-page="10"
            :current-page="currentPage"
            :pageCount="pageCount"
            @pagechanged="onPageChange"
          />
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { VueDatePicker } from "@mathieustan/vue-datepicker";
import { actionTypes } from "@/store/modules/testing";
import { mapState } from "vuex";
import TestingReportTable from "./TestingReportTable.vue";
import Pagination from "../Pagination/Pagination.vue";

// import axios from "axios";
export default {
  name: "AppTestingActiveReport",
  data() {
    return {
      color: "#192c8c",
      date: "",
      currentPage: 1,

      isSubmiting: false,
    };
  },
  props: {
    //число объектов на странице
    size: {
      type: Number,
      required: false,
      default: 10,
    },
  },
  components: {
    VueDatePicker,
    TestingReportTable,
    Pagination,
  },
  computed: {
    ...mapState({
      dataReport: (state) => state.testing.dataReport,
    }),
    isComplete() {
      return this.date != "";
    },
    //PAGINATION
    //количество страниц
    totalPages() {
      if (this.dataReport !== []) {
        return Math.ceil(this.dataReport.length / this.size);
      } else {
        return 0;
      }
    },
    //текущая страница

    //пагинация
    paginatedData() {
      const start = (this.currentPage - 1) * this.size,
        end = start + this.size;
      return this.dataReport.slice(start, end);
    },
    pageCount() {
      if (this.dataReport !== []) {
        let l = this.dataReport.length,
          s = this.size;
        return Math.ceil(l / s);
      } else {
        return 0;
      }
    },
    //
    colorArrowLeft() {
      if (this.pageNumber === 0) {
        return "#7C8DB5";
      } else {
        return "#192c8c";
      }
    },
    colorArrowRight() {
      if (this.pageNumber >= this.pageCount - 1) {
        return "#7C8DB5";
      } else {
        return "#192c8c";
      }
    },
  },
  methods: {
    downloadFile() {
      this.$store
        .dispatch(actionTypes.downloadFile, this.date)
        .then((response) => {
          const url = URL.createObjectURL(
            new Blob([response.data], {
              type: "application/vnd.ms-excel",
            })
          );
          var fileName = `Отчет тестирования за ${this.date.start} - ${this.date.end}`;
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", fileName);
          document.body.appendChild(link);
          link.click();
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
    createReport() {
      this.$store
        .dispatch(actionTypes.getDateReport, this.date)
        .then(() => {
          this.isSubmiting = true;
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
    //ПАГИНАЦИЯ
    onPageChange(page) {
      this.currentPage = page;
    },
  },
};
</script>

