<template>
  <tr class="tr-info-body">
    <td class="td number">{{ idx + 1 }}</td>
    <td class="td number">{{ item_device.imei }}</td>
    <td class="td">{{ item_device.fio }}</td>
    <td class="td number">{{ item_device.version }}</td>
    <td class="td number">{{ item_device.startTime }}</td>
    <td class="td number">{{ item_device.endTime }}</td>
    <!-- {{ item_device.isFirmware }} -->
    <td class="td">
      <div class="block-firm" :class="defineColorFire">
        {{ this.defineFirmed }}
      </div>
    </td>
    <td class="td">
      <div class="block-firm" :class="defineColorTest">
        {{ this.defineTest }}
      </div>
    </td>
  </tr>
</template>

<script>
export default {
  name: "AppItemTableDevice",
  data() {
    return {};
  },
  computed: {
    defineColorFire() {
      return {
        "firm-yes": this.item_device.isFirmware,
        "firm-no": !this.item_device.isFirmware,
      };
    },
    defineColorTest() {
      return {
        "firm-yes": this.item_device.isTest,
        "firm-no": !this.item_device.isTest,
      };
    },
    defineFirmed() {
      if (this.item_device.isFirmware) {
        return "Прошит";
      } else {
        return "Не прошит";
      }
    },
    defineTest() {
      if (this.item_device.isTest) {
        return "ДА";
      } else {
        return "НЕТ";
      }
    },
  },
  props: {
    item_device: {
      type: Object,
      default() {
        return {};
      },
    },
    idx: {
      type: Number,
      default() {
        return {};
      },
    },
  },
};
</script>

<style></style>
