<template>
  <div class="profile">
    <Header title="Профиль" description="Данные профиля" />
    <div class="container-page">
      <div class="profile__wrap">
        <div class="profile__img">
          <img src="../assets/svg/Profile.svg" alt="profile" />
        </div>
        <div class="profile__info">
          <div class="profile__info-block">
            <div class="profile__wrap-label">
              <p class="index-text500">ФИО</p>
            </div>
            <p class="index-text400">
              {{ infoUser.lastName }} {{ infoUser.name }} {{ infoUser.surname }}
            </p>
          </div>
          <div class="profile__info-block">
            <div class="profile__wrap-label">
              <p class="index-text500">Телефон</p>
            </div>
            <p class="index-number400">8-903-903-11-11</p>
          </div>
          <div class="profile__info-block">
            <div class="profile__wrap-label">
              <p class="index-text500">Email</p>
            </div>
            <p class="index-text400">
              {{ infoUser.username }}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import { mapState } from "vuex";
export default {
  components: { Header },
  name: "AppProfile",
  data() {
    return {
      title: "Профиль",
    };
  },
  computed: {
    ...mapState({
      infoUser: (state) => state.auth.infoUser,
      token: (state) => state.auth.token,
    }),
  },
  beforeMount() {
    document.title = this.title;
  },
  beforeUpdate() {
    document.title = this.title;
  },
  mounted() {
    if (
      this.token === "undefined" ||
      this.token === "" ||
      this.infoUser === null
    ) {
      this.$router.push({ name: "Login" });
    }
  },
};
</script>

