<template>
  <transition name="modal" @click="$emit('close')">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container max-width">
          <div class="modal-header">
            <p class="title-modal">Изменение пользователя</p>
            <div class="modal-header__cross" @click="$emit('close')">
              <img src="../../assets/svg/CrossModal.svg" alt="cross" />
            </div>
          </div>
          <div class="error" v-if="isSubmitted && this.$v.$invalid">
            <p class="error-text">*Заполните все пустые поля</p>
          </div>
          <div class="error-messages">
            <ValidationErrors
              v-if="validationErrors"
              :validation-errors="validationErrors"
            />
          </div>
          <form id="#modalEditUser">
            <div class="modal-body">
              <div class="modal-body__wrap-input">
                <label for="lastName" class="label-for-input">Фамилия</label>
                <input
                  type="text"
                  name="lastName"
                  :value="item_user.lastName"
                  placeholder="Введите фамилию"
                  class="input-text"
                  :class="{
                    'error-border-input':
                      isSubmitted && $v.putData.lastName.$error,
                  }"
                />
                <img
                  src="~@/assets/svg/error.svg"
                  alt="error"
                  v-if="isSubmitted && $v.putData.lastName.$error"
                  class="wrapValidationInput__error"
                />
              </div>
              <div class="modal-body__wrap-input">
                <label for="name" class="label-for-input">Имя</label>
                <input
                  type="text"
                  name="name"
                  placeholder="Введите имя"
                  :value="item_user.name"
                  class="input-text"
                  :class="{
                    'error-border-input': isSubmitted && $v.putData.name.$error,
                  }"
                />
                <img
                  src="~@/assets/svg/error.svg"
                  alt="error"
                  v-if="isSubmitted && $v.putData.name.$error"
                  class="wrapValidationInput__error"
                />
              </div>

              <div class="modal-body__wrap-input">
                <label for="surname" class="label-for-input">Отчество</label>
                <input
                  type="text"
                  name="surname"
                  :value="item_user.surname"
                  placeholder="Введите отчество"
                  class="input-text"
                  :class="{
                    'error-border-input':
                      isSubmitted && $v.putData.surname.$error,
                  }"
                />
                <img
                  src="~@/assets/svg/error.svg"
                  alt="error"
                  v-if="isSubmitted && $v.putData.surname.$error"
                  class="wrapValidationInput__error"
                />
              </div>
              <div class="modal-body__wrap-input">
                <label for="email" class="label-for-input">Email</label>
                <input
                  type="text"
                  name="email"
                  :value="item_user.email"
                  placeholder="Введите email"
                  class="input-text"
                  :class="{
                    'error-border-input':
                      isSubmitted && $v.putData.email.$error,
                  }"
                />
                <img
                  src="~@/assets/svg/error.svg"
                  alt="error"
                  v-if="isSubmitted && $v.putData.email.$error"
                  class="wrapValidationInput__error"
                />
              </div>
              <div class="modal-body__wrap-input">
                <label for="phone" class="label-for-input">Телефон</label>
                <the-mask
                  :mask="['+#(###)-###-##-##']"
                  class="input-number"
                  ref="phoneNumber"
                  placeholder="+7-(***)-(***)-****"
                  name="contactPhone"
                  :value="item_user.contactPhone"
                  :class="{
                    'error-border-input':
                      isSubmitted && $v.putData.contactPhone.$error,
                  }"
                />
                <img
                  src="~@/assets/svg/error.svg"
                  alt="error"
                  v-if="isSubmitted && $v.putData.contactPhone.$error"
                  class="wrapValidationInput__error"
                />
              </div>
              <div class="modal-body__wrap-input">
                <label for="role" class="label-for-input">Назначить роль</label>
                <select
                  name="role"
                  class="select"
                  :value="item_user.role"
                  :class="{
                    'error-border-input': isSubmitted && $v.putData.role.$error,
                  }"
                >
                  <option value="TESTER">Тестирование</option>
                  <option value="FIRMWARE">Прошивка</option>
                  <option value="STICKERS">Печать</option>
                  <option value="ADMIN">Администратор</option>
                </select>
              </div>
            </div>
            <div class="modal-footer">
              <div class="modal-footer__btn">
                <button
                  type="button"
                  class="index-btn lite-color"
                  @click="$emit('close')"
                >
                  Отменить
                </button>
              </div>
              <div class="modal-footer__btn">
                <button
                  @click="safeInfoUser"
                  type="button"
                  class="index-btn index-color"
                >
                  Сохранить
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { TheMask } from "vue-the-mask";
import { required } from "vuelidate/lib/validators";
import { actionTypes } from "@/store/modules/users";
import { mapState } from "vuex";
import ValidationErrors from "../ValidationErrors.vue";
export default {
  name: "AppModalEditUser",
  data() {
    return {
      isSubmitted: false,
      putData: [],
    };
  },
  validations: {
    putData: {
      name: {
        required,
      },
      surname: {
        required,
      },
      lastName: {
        required,
      },

      email: {
        required,
      },
      role: {
        required,
      },
      contactPhone: {
        required,
      },
    },
  },
  components: { TheMask, ValidationErrors },
  props: {
    item_user: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    ...mapState({
      validationErrors: (state) => state.users.validationErrors,
    }),
  },
  methods: {
    safeInfoUser() {
      const formData = new FormData(document.getElementById("#modalEditUser"));
      this.putData = [];
      for (let [name, value] of formData) {
        this.putData = { ...this.putData, [name]: value };
        console.log([name], value);
      }
      this.putData = {
        ...this.putData,
        userId: this.item_user.userId,
      };
      this.isSubmitted = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
        return console.log("не ок");
      } else {
        //  Изменение группы
        this.$store
          .dispatch(actionTypes.editUser, this.putData)
          .then(() => {
            this.$emit("close");
          })
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
  },
};
</script>

<style></style>
