<template>
  <div class="testing">
    <Header title="Тестирование" description="Анализ тестирования блоков" />
    <div class="container-page">
      <div class="data-tab">
        <div class="data-tab__wrap">
          <div
            class="data-tab__btn"
            @click="activeBlock('test')"
            :class="{ 'active-btn': this.isActiveTest }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveTest }"
            >
              Тесты
            </p>
          </div>
          <div
            class="data-tab__btn"
            @click="activeBlock('report')"
            :class="{ 'active-btn': this.isActiveReport }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveReport }"
            >
              Отчеты
            </p>
          </div>
          <div
            class="data-tab__btn"
            @click="activeBlock('statistics')"
            :class="{ 'active-btn': this.isActiveStatistics }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveStatistics }"
            >
              Статистика
            </p>
          </div>
        </div>
      </div>
      <div class="testing__search" v-if="this.isActiveTest">
        <input
          type="text"
          placeholder="Поиск по IMEI"
          class="testing__input number"
          v-model="searchIMEIs"
        />
        <img
          src="../assets/svg/search.svg"
          alt="search"
          class="testing__input-svg"
        />
      </div>
      <div class="testing__table-container" v-if="this.isActiveTest">
        <TableTesting
          :imei="searchIMEIs"
          :pagination_data="this.paginatedData"
        />
        <template>
          <pagination
            :total-pages="this.totalPages"
            :per-page="10"
            :current-page="currentPage"
            :pageCount="pageCount"
            @pagechanged="onPageChange"
          />
        </template>
      </div>
      <div class="testing__report-container" v-if="this.isActiveReport">
        <testing-active-report />
      </div>
      <div class="testing__statistics-container" v-if="this.isActiveStatistics">
        <testing-active-statistics />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
// import { actionTypesSocket } from "@/store/modules/socket";
import Header from "../components/Header.vue";
import TableTesting from "../components/Table/TableTesting.vue";
import { actionTypes } from "@/store/modules/testing";
import TestingActiveStatistics from "../components/Testing/TestingActiveStatistics.vue";
import TestingActiveReport from "../components/Testing/TestingActiveReport.vue";
import Pagination from "../components/Pagination/Pagination.vue";

export default {
  name: "AppTesting",

  data() {
    return {
      searchIMEIs: "",
      currentPage: 1,
      // pageNumber: 0,
      isActiveTest: true,
      isActiveReport: false,
      isActiveStatistics: false,
      date: "",
      title: "Тестирование",
      color: "#192c8c",
    };
  },
  props: {
    //число объектов на странице
    size: {
      type: Number,
      required: false,
      default: 10,
    },
    isImeiData: {
      type: Boolean,
      required: false,
    },
  },

  components: {
    Header,
    TableTesting,

    TestingActiveStatistics,
    TestingActiveReport,
    Pagination,
  },
  computed: {
    ...mapState({
      allTests: (state) => state.testing.allTests,
      // searchIMEI: (state) => state.testing.searchIMEI,
      token: (state) => state.auth.token,
      infoUser: (state) => state.auth.infoUser,
      isConnected: (state) => state.socket.isConnected,
    }),
    searchIMEIReact() {
      return this.searchIMEIs;
    },
    //количество страниц
    totalPages() {
      if (this.allTests !== []) {
        const total = Math.ceil(this.allTests.length / this.size);
        return total;
      } else {
        return 0;
      }
    },
    //текущая страница

    //пагинация
    paginatedData() {
      const start = (this.currentPage - 1) * this.size,
        end = start + this.size;
      return this.allTests.slice(start, end);
    },
    pageCount() {
      if (this.allTests !== []) {
        let l = this.allTests.length,
          s = this.size;
        return Math.ceil(l / s);
      } else {
        return 0;
      }
    },
  },
  methods: {
    //TAB
    activeBlock(tab) {
      if (tab === "test") {
        this.isActiveTest = true;
        this.isActiveReport = false;
        this.isActiveStatistics = false;
      } else if (tab === "report") {
        this.isActiveTest = false;
        this.isActiveReport = true;
        this.isActiveStatistics = false;
      } else if (tab === "statistics") {
        this.isActiveTest = false;
        this.isActiveReport = false;
        this.isActiveStatistics = true;
      }
    },
    //ПАГИНАЦИЯ
    onPageChange(page) {
      this.currentPage = page;
    },
  },

  watch: {
    searchIMEIReact() {
      let imei = this.searchIMEIReact;

      if (imei !== "") {
        // this.currentPage = 0;
        this.$store
          .dispatch(actionTypes.getSearch, imei)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      } else {
        this.$store
          .dispatch(actionTypes.getAllTests)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
  },
  beforeMount() {
    document.title = this.title;
  },
  beforeUpdate() {
    document.title = this.title;
  },
  mounted() {
    if (
      this.token === "undefined" ||
      this.token === "" ||
      this.infoUser === null
    ) {
      this.$router.push({ name: "Login" });
    }
    //ПОДКЛЮЧЕНИЕ СОКЕТОВ ВРЕМЕННО ОТКЛЮЧЕНО
    // if (this.infoUser.id && !this.isConnected) {
    //   this.$store.dispatch(actionTypesSocket.socketConnect, this.infoUser.id);
    // }

    this.$store
      .dispatch(actionTypes.getAllTests)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>

