<template>
  <Bar :chart-data="chartData" :chartOptions="chartOptions" />
</template>

<script>
// DataPage.vue
import { Bar } from "vue-chartjs";
import {
  Chart,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
} from "chart.js";
import { mapState } from "vuex";

Chart.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale);

export default {
  name: "FirmwareChart",

  data() {
    return {
      chartOptions: {
        legend: {
          display: false, // отключает легенду, бесполезную когда на графике одна линия
        },
        plugins: {
          legend: {
            display: false,
          },
        },
        scales: {
          x: {
            ticks: {
              autoSkip: false,
              maxRotation: 90,
              minRotation: 90,
            },
          },
        },
      },
    };
  },
  components: { Bar },
  computed: {
    ...mapState({
      dataBarStat: (state) => state.firmware.dataBarStatFirm,
      loading: (state) => state.testing.loading,
    }),
    chartData() {
      return {
        labels: this.dataBarStat.map((c) => c.date),
        datasets: [
          {
            label: "Кол-во прошитых устройств",
            backgroundColor: [
              "rgba(25, 44, 140, 0.8)",
              "rgba(15, 49, 169, 0.8)",
              "rgba(15, 40, 169, 0.5)",
              "rgba(147, 170, 252, 0.8)",
              "rgba(198, 210, 253, 0.8)",
              "rgba(229, 234, 252, 0.8)",
              "rgba(201, 203, 207, 0.2)",
              "rgba(15, 40, 169, 0.5)",
              "rgba(147, 170, 252, 0.8)",
              "rgba(198, 210, 253, 0.8)",
              "rgba(229, 234, 252, 0.8)",
              "rgba(201, 203, 207, 0.2)",
            ],
            borderColor: [
              "rgb(25, 44, 140)",
              "rgb(15, 49, 169)",
              "rgb(15, 49, 169)",
              "rgb(147, 170, 252)",
              "rgb(198, 210, 253)",
              "rgb(229, 234, 252)",
              "rgb(201, 203, 207)",
              "rgb(15, 49, 169)",
              "rgb(147, 170, 252)",
              "rgb(198, 210, 253)",
              "rgb(229, 234, 252)",
              "rgb(201, 203, 207)",
            ],
            borderWidth: 2,
            borderRadius: 5,
            data: this.dataBarStat.map((c) => c.counter),

            maxBarThickness: 25,
            // по умолчанию точки прозрачные, что не всегда красиво
            pointRadius: 5,
          },
        ],
      };
    },
  },
};
</script>
