// import axios from "axios";
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";
import testing from "./testing";
import firmware from "./firmware";
import auth from "./auth";
import { urlForSockJS } from "@/api/socket.js";
const state = {
  //   socket: new SockJS(urlForSockJS),
  isConnected: false,
  message: {},
  messageDimmer: {},
  reconnectError: false,
  stompClient: null,
  recInterval: null,
};
export const new_conn = function () {
  if (!state.isConnected) {
    var newSoket = new SockJS(urlForSockJS);
    //  this.socket = new SockJS(urlForSockJS);
    state.stompClient = Stomp.over(newSoket, {
      debug: false,
    });
    var headers = { "user-id": auth.state.infoUser.id };
    state.stompClient.connect(
      headers,
      (frame) => {
        console.log(frame, "Соединение восстановлено");
        state.recInterval = null;
        state.isConnected = true;
        state.stompClient.subscribe("/topic/testing", (tick) => {
          console.log("Подписка на тестирование");
          const messageTesting = JSON.parse(tick.body);
          let newElem = testing.state.allTests.find((element) => {
            return element.id === messageTesting.id;
          });
          console.log(newElem, "Найденный элемент");
          if (newElem) {
            console.log("Заменил тестирование");
            var indexElem = testing.state.allTests.indexOf(newElem);
            testing.state.allTests.splice(indexElem, 1);
            testing.state.allTests.unshift(messageTesting);
          } else {
            console.log("Добавил тестирование");
            testing.state.allTests.unshift(messageTesting);
          }
        });
        //Подписка на сообщения от блоков таймера
        state.stompClient.subscribe("/user/queue/firmware", (tick) => {
          console.log("Подписка на прошивку", tick);
          const messageFirmware = JSON.parse(tick.body);
          let newElem = firmware.state.allDevice.find((element) => {
            return element.id === messageFirmware.id;
          });
          if (newElem) {
            console.log("Заменил прошивку");
            var indexElem = firmware.state.allDevice.indexOf(newElem);
            firmware.state.allDevice.splice(indexElem, 1);
            firmware.state.allDevice.unshift(messageFirmware);
          } else {
            console.log("Добавил прошивку");
            firmware.state.allDevice.unshift(messageFirmware);
          }
        });
      },
      (error) => {
        state.isConnected = false;
        console.log(error, "❌ Socket Error!!!");
      }
    );
  }
};
export const mutationTypes = {
  SOCKET_ONOPEN: "✅ Socket connected!",
  SOCKET_ONCLOSE: "❌ Socket disconnected!",
  SOCKET_ONCLOSE_EXIT: "❌ Socket disconnected exit!",
  SOCKET_ONERROR: "❌ Socket Error!!!",
  SOCKET_SENDMESSAGE: "Socket Send Message",
  SOCKET_ONMESSAGE_TESTING: "Websocket message received TESTING",
  SOCKET_ONMESSAGE_FIRMWARE: "Websocket message received FIRMWARE",
  SOCKET_RECONNECT: "Websocket reconnected",
  SOCKET_RECONNECT_ERROR: "Websocket is having issues reconnecting..",
};
export const actionTypesSocket = {
  socketConnect: "[socket] socketConnect",
  // socketSendMessage: "[soket]socketSendMessage",
  socketAsseptMessage: "[socket] socketAsseptMessage",
  socketClose: "[socket] socketClose",
};

export const mutations = {
  [mutationTypes.SOCKET_ONMESSAGE_TESTING](state, messageTesting) {
    console.log(messageTesting, "это тик приходит");
    let newElem = testing.state.allTests.find((element) => {
      return element.id === messageTesting.id;
    });
    console.log(newElem, "Найденный элемент");
    if (newElem) {
      console.log("Заменил тестирование");
      var indexElem = testing.state.allTests.indexOf(newElem);
      testing.state.allTests.splice(indexElem, 1);
      testing.state.allTests.unshift(messageTesting);
    } else {
      console.log("Добавил тестирование");
      testing.state.allTests.unshift(messageTesting);
    }
  },
  [mutationTypes.SOCKET_ONMESSAGE_FIRMWARE](state, messageFirmware) {
    console.log(messageFirmware, "это тик приходит");
    let newElem = firmware.state.allDevice.find((element) => {
      return element.id === messageFirmware.id;
    });
    if (newElem) {
      var indexElem = firmware.state.allDevice.indexOf(newElem);
      console.log("Заменил прошивку");
      firmware.state.allDevice.splice(indexElem, 1);
      firmware.state.allDevice.unshift(messageFirmware);
    } else {
      console.log("Добавил прошивку");
      firmware.state.allDevice.unshift(messageFirmware);
    }
  },
  [mutationTypes.SOCKET_ONOPEN](state) {
    state.isConnected = true;
    console.log("✅ Socket connected!");
  },
  [mutationTypes.SOCKET_ONCLOSE](state, event) {
    state.isConnected = false;
    console.log(event, "❌ Socket disconnected!");
    state.recInterval = window.setInterval(function () {
      new_conn();
    }, 5000);
    console.log(event, "❌ Socket disconnected!");
  },
  [mutationTypes.SOCKET_ONERROR](event) {
    state.isConnected = false;
    console.log(event);
  },
  // mutations for reconnect methods
  [mutationTypes.SOCKET_RECONNECT](state, count) {
    console.info(state, count);
  },
  [mutationTypes.SOCKET_RECONNECT_ERROR](state) {
    state.socket.reconnectError = true;
  },
  [mutationTypes.SOCKET_ONCLOSE_EXIT](state) {
    state.isConnected = false;
    state.socket.onclose = function () {
      console.log(event, "❌ Socket disconnected exit!");
    };
    state.stompClient.disconnect();
  },
};
const actions = {
  [actionTypesSocket.socketClose](context) {
    context.commit(mutationTypes.SOCKET_ONCLOSE_EXIT);
  },
  [actionTypesSocket.socketConnect](context, id) {
    if (state.isConnected) return;
    console.log("Начал попытку соединения...");
    this.socket = new SockJS(urlForSockJS);
    //  var newSoket = new SockJS(urlForSockJS);
    state.stompClient = Stomp.over(this.socket, {
      debug: false,
    });
    var headers = { "user-id": id };
    state.stompClient.connect(
      headers,
      () => {
        context.commit(mutationTypes.SOCKET_ONOPEN);
        //Подписка на тестирование
        state.stompClient.subscribe(
          "/topic/testing",
          (tick) => {
            console.log("Подписка на тестирование", tick);
            tick.ack();
            const itemMessageTest = JSON.parse(tick.body);
            context.commit(
              mutationTypes.SOCKET_ONMESSAGE_TESTING,
              itemMessageTest
            );
          },
          { ack: "client" }
        );
        //Подписка на прошивку
        state.stompClient.subscribe(
          "/user/queue/firmware",
          (tick) => {
            tick.ack();
            console.log("Подписка на прошивку", tick);
            const itemMessageFirmware = JSON.parse(tick.body);
            context.commit(
              mutationTypes.SOCKET_ONMESSAGE_FIRMWARE,
              itemMessageFirmware
            );
          },
          { ack: "client" }
        );
      },
      (error) => {
        console.log(error, "❌ Socket Error!!!");
        context.commit(mutationTypes.SOCKET_ONCLOSE);

        // setTimeout(this.dispatch(actionTypesSocket.socketConnect), 1000);
      }
    );
  },
};
export default {
  state,
  mutations,
  actions,
};
