<template>
  <table class="table-testing">
    <thead class="thead">
      <tr class="tr">
        <th class="th">№</th>
        <th class="th">IMEI</th>
        <th class="th">ФИО</th>
        <th class="th">Версия прошивки</th>
        <th class="th">Время начала</th>
        <th class="th">Время конца</th>
        <th class="th">Есть ли прошивка?</th>
        <th class="th">Протестирован ли?</th>
      </tr>
    </thead>
    <tbody class="tbody">
      <template v-if="this.pagination_data.length !== 0">
        <ItemTableDevice
          v-for="(device, index) in this.pagination_data"
          :key="index"
          :item_device="device"
          :idx="index"
        />
      </template>

      <template v-if="this.pagination_data.length === 0 && isLoading">
        <tr class="tr-transponent">
          <th class="th" colspan="11"></th>
        </tr>
        <tr class="tr-loader">
          <th class="" colspan="8">
            <div class="lds-roller">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </th>
        </tr>
      </template>
      <template v-if="this.pagination_data.length === 0 && !isLoading">
        <div class="error">
          <p class="error-text">*Устройств по данному IMEI не найдено</p>
        </div>
      </template>
    </tbody>
  </table>
</template>

<script>
import { mapState } from "vuex";
import ItemTableDevice from "./ItemTableDevice.vue";
export default {
  name: "AppTableDevice",
  data() {
    return {};
  },
  props: {
    imei: {
      type: String,
      default() {
        return {};
      },
      required: false,
    },
    pagination_data: {
      type: Array,
      default() {
        return {};
      },
    },
  },

  components: { ItemTableDevice },

  computed: {
    ...mapState({
      allDevice: (state) => state.firmware.allDevice,
      isLoading: (state) => state.firmware.isLoading,
    }),
    sortedData() {
      if (this.imei.length === 0) {
        return this.pagination_data;
      } else {
        return this.searchIMEI;
      }
    },
  },
};
</script>

