<template>
  <div class="firmware">
    <Header title="Прошивка" description="Анализ прошивок устройств" />
    <div class="container-page">
      <div class="data-tab">
        <div class="data-tab__wrap">
          <div
            class="data-tab__btn"
            @click="activeBlock('test')"
            :class="{ 'active-btn': this.isActiveTest }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveTest }"
            >
              Тесты
            </p>
          </div>
          <div
            class="data-tab__btn"
            @click="activeBlock('report')"
            :class="{ 'active-btn': this.isActiveReport }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveReport }"
            >
              Отчеты
            </p>
          </div>
          <div
            class="data-tab__btn"
            @click="activeBlock('statistics')"
            :class="{ 'active-btn': this.isActiveStatistics }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveStatistics }"
            >
              Статистика
            </p>
          </div>
        </div>
      </div>
      <div class="testing__search" v-if="this.isActiveTest">
        <input
          type="text"
          placeholder="Поиск по IMEI"
          class="testing__input number"
          v-model="searchIMEIs"
        />
        <img
          src="../assets/svg/search.svg"
          alt="search"
          class="testing__input-svg"
        />
      </div>
      <div class="testing__table-container" v-if="this.isActiveTest">
        <!-- <table-loader v-if="isLoading" /> -->
        <table-device
          :imei="searchIMEIs"
          :pagination_data="this.paginatedData"
        />

        <template>
          <pagination
            :total-pages="this.totalPages"
            :per-page="10"
            :current-page="currentPage"
            :pageCount="pageCount"
            @pagechanged="onPageChange"
          />
        </template>
      </div>
      <div class="testing__report-container" v-if="this.isActiveReport">
        <firmware-active-report />
      </div>
      <div class="testing__statistics-container" v-if="this.isActiveStatistics">
        <!-- <testing-active-statistics /> -->
        <firmware-active-statistics />
      </div>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import { actionTypes } from "@/store/modules/firmware";
// import { actionTypesSocket } from "@/store/modules/socket";
import { mapState } from "vuex";

import TableDevice from "../components/Table/TableDevice.vue";
import FirmwareActiveReport from "../components/Firmware/FirmwareActiveReport.vue";
import FirmwareActiveStatistics from "../components/Firmware/FirmwareActiveStatistics.vue";
import Pagination from "../components/Pagination/Pagination.vue";
// import TableLoader from "../components/Table/TableLoader.vue";
export default {
  name: "AppFirmWare",
  data() {
    return {
      searchIMEIs: "",
      currentPage: 1,
      // pageNumber: 0,
      isActiveTest: true,
      isActiveReport: false,
      isActiveStatistics: false,
      title: "Прошивка",
    };
  },
  props: {
    //число объектов на странице
    size: {
      type: Number,
      required: false,
      default: 10,
    },
  },
  components: {
    Header,

    TableDevice,
    FirmwareActiveReport,
    FirmwareActiveStatistics,
    Pagination,
    // TableLoader,
  },
  // watch: {

  // { deep: true }
  // }

  computed: {
    ...mapState({
      allFirmware: (state) => state.firmware.allFirmware,
      isLoading: (state) => state.firmware.isLoading,
      token: (state) => state.auth.token,
      infoUser: (state) => state.auth.infoUser,
      isConnected: (state) => state.socket.isConnected,
    }),
    searchIMEIReact() {
      return this.searchIMEIs;
    },
    //количество страниц
    totalPages() {
      if (this.allFirmware !== []) {
        return Math.ceil(this.allFirmware.length / this.size);
      } else {
        return 0;
      }
    },
    //текущая страница

    //пагинация
    paginatedData() {
      const start = (this.currentPage - 1) * this.size,
        end = start + this.size;
      return this.allFirmware.slice(start, end);
    },
    pageCount() {
      if (this.allFirmware !== []) {
        let l = this.allFirmware.length,
          s = this.size;
        return Math.ceil(l / s);
      } else {
        return 0;
      }
    },
  },
  methods: {
    //TAB
    activeBlock(tab) {
      if (tab === "test") {
        this.isActiveTest = true;
        this.isActiveReport = false;
        this.isActiveStatistics = false;
      } else if (tab === "report") {
        this.isActiveTest = false;
        this.isActiveReport = true;
        this.isActiveStatistics = false;
      } else if (tab === "statistics") {
        this.isActiveTest = false;
        this.isActiveReport = false;
        this.isActiveStatistics = true;
      }
    },
    //ПАГИНАЦИЯ
    onPageChange(page) {
      this.currentPage = page;
    },
  },
  watch: {
    searchIMEIReact() {
      let imei = this.searchIMEIReact;

      if (imei !== "") {
        this.$store
          .dispatch(actionTypes.getSearchIMEI, imei)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      } else {
        this.$store
          .dispatch(actionTypes.getAllFirmware)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
  },
  beforeMount() {
    document.title = this.title;
  },
  beforeUpdate() {
    document.title = this.title;
  },
  mounted() {
    if (
      this.token === "undefined" ||
      this.token === "" ||
      this.infoUser === null
    ) {
      this.$router.push({ name: "Login" });
    }
    //ПОДКЛЮЧЕНИЕ СОКЕТОВ
    // if (this.infoUser.id && !this.isConnected) {
    //   this.$store.dispatch(actionTypesSocket.socketConnect, this.infoUser.id);
    // }
    this.$store
      .dispatch(actionTypes.getAllFirmware)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>

