import axios from "@/api/axios";
const getAllFirmware = () => {
  return axios.get(`/all_firmware`);
};
const getSearchIMEI = (imei) => {
  return axios.get(`/firmware/search_firmware/${imei}`);
};
const getDataStatFirm = (date) => {
  return axios.post(`/firmware/get_stat`, date);
};

const getDateReportFirm = (date) => {
  return axios.post(`/firmware/get_data_to_date`, date);
};
const downloadFileFirm = (data) => {
  return axios.post(`/firmware/export_excel`, data, {
    responseType: "blob",
  });
};
const getAvgDataFirm = (data) => {
  return axios.post(`/firmware/get_avg_data`, data);
};

export default {
  getAllFirmware,
  getSearchIMEI,
  getDataStatFirm,
  getDateReportFirm,
  downloadFileFirm,
  getAvgDataFirm,
};
