<template>
  <div class="device-block__column">
    <div class="device-block__item">
      <div class="mini-menu" v-bind:class="{ 'open-menu': isOpenMiniMenu }">
        <div class="mini-menu__svg" @click="openMiniMenu">
          <img src="../../assets/svg/CrossWhite.svg" alt="cross" />
        </div>
        <div class="mini-menu__wrap">
          <ul class="dropdown-menu">
            <li @click="openTestingPage">
              <p class="dropdown-menu__link">Информация о тестировании</p>
            </li>
            <li>
              <p class="dropdown-menu__link">Информация о прошивке</p>
            </li>
          </ul>
        </div>
      </div>
      <div class="device-block__info">
        <div
            class="device-block__pdf"
            @click="downloadFile"
        >
          <div class="report__download-icon" tooltip="Скачать">
            <img src="../../assets/svg/download.svg" alt="download" />
          </div>
          <div class="report__download-text">Скачать PDF</div>
        </div>
        <div class="device-block__text">
          <div class="device-block__title">
            <p class="label-for-input">IMEI:</p>
          </div>
          <div class="device-block__description">
            <p class="index-text400 number">{{ device_item.imei }}</p>
          </div>
        </div>
        <div class="device-block__text">
          <div class="device-block__title">
            <p class="label-for-input">Серийный номер:</p>
          </div>
          <div class="device-block__description">
            <p class="index-text400 number">{{ device_item.serial }}</p>
          </div>
        </div>
        <div class="device-block__text">
          <div class="device-block__title">
            <p class="label-for-input">Прошивал(-a):</p>
          </div>
          <div class="device-block__description">
            <p class="index-text400">{{ device_item.firmwareFio }}</p>
          </div>
        </div>
        <div class="device-block__text">
          <div class="device-block__title">
            <p class="label-for-input">Тестировал(-a):</p>
          </div>
          <div class="device-block__description">
            <p class="index-text400">{{ device_item.testFio }}</p>
          </div>
        </div>
      </div>
      <div class="device-block__btn-wrap">
        <div class="device-block__btn">
          <button
            type="button"
            class="index-btn lite-index-color"
            @click="openTestingPage"
          >
            Подробная информация
          </button>
        </div>
      </div>
      <div class="device-block__kod">
        <div class="device-block__title">
          <p class="label-for-input">CCID:</p>
        </div>
        <div class="device-block__kod-img">
          <barcode
            v-bind:value="device_item.ccid"
            class="barcode device-block__kod-svg"
          >
          </barcode>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueBarcode from "vue-barcode";
import { actionTypesDevice } from '@/store/modules/device'

export default {
  name: "AppItemDevice",
  data() {
    return {
      isOpenMiniMenu: false,
    };
  },
  props: {
    device_item: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    openMiniMenu() {
      this.isOpenMiniMenu = !this.isOpenMiniMenu;
    },
    openTestingPage() {
      this.$router.push({
        path: `/information/${this.device_item.imei}`,
      });
    },
    downloadFile() {
      this.$store
          .dispatch(actionTypesDevice.downloadPdfDeviceItem, {imei:this.device_item.imei})
          .then((response) => {
            console.log(response, 'response PDF');
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            const fileName = `${this.device_item.imei}.pdf`; // Измените это на желаемое имя файла
            link.setAttribute('download', fileName); // Устанавливаем имя файла
            document.body.appendChild(link);
            link.click(); // Симулируем клик для скачивания
            document.body.removeChild(link); // Удаляем ссылку после скачивания
            window.URL.revokeObjectURL(url); // Освобождаем ресурсы
          })
          .catch((er) => {
            console.log("Ошибка", er);
          });
    }
  },
  components: {
    barcode: VueBarcode,
  },
  mounted() {},
};
</script>

