<template>
  <div class="item-card-user">
    <div class="item-card-user__about-people">
      <div class="indicator-work" v-bind:class="defineColorStatus">
        <div class="crush" @click="deleteItem()">
          <img src="../../assets/svg/trash.svg" alt="trash" />
        </div>
        <div class="edit" @click="editItem()">
          <img src="../../assets/svg/edit.svg" alt="edit" />
        </div>
      </div>
      <div class="item-card-user__about-people-info">
        <p class="index-text400 center">
          {{ item_user.lastName }} {{ item_user.name }}
          {{ item_user.surname }}
        </p>
        <p class="index-text500">{{ this.role }}</p>
      </div>
    </div>
    <div class="item-card-user__info">
      <div class="item-card-user__info-text">
        <div class="item-card-user__wrap-text">
          <p class="index-text500">IP адрес:</p>
        </div>
        <p class="index-number400">{{ item_user.ipAddress }}</p>
      </div>
      <div class="item-card-user__info-text">
        <div class="item-card-user__wrap-text">
          <p class="index-text500">Время последнего взаимодействия:</p>
        </div>
        <p class="index-number400">{{ item_user.lastEditTime }}</p>
      </div>
    </div>
    <div class="item-card-user__status">
      <div class="curcle" v-bind:class="defineColorStatus"></div>
      <p class="index-text500">{{ this.defineNameStatus }}</p>
    </div>
    <ModalDeleteUser
      v-if="isModalDeleteOpen"
      @close="isModalDeleteOpen = false"
      :item_user="this.item_user"
    />
  </div>
</template>

<script>
// import { actionTypes } from "@/store/modules/users";
import ModalDeleteUser from "../Modal/ModalDeleteUser.vue";
export default {
  components: { ModalDeleteUser },
  name: "AppItemCardUser",
  data() {
    return {
      isModalDeleteOpen: false,
    };
  },
  props: {
    item_user: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    role() {
      if (this.item_user.role === "FIRMWARE") {
        return "Прошивка";
      } else if (this.item_user.role === "TESTER") {
        return "Тестирование";
      } else if (this.item_user.role === "STICKERS") {
        return "Печать";
      } else if (this.item_user.role === "ADMIN") {
        return "Администратор";
      } else {
        return "Не определено";
      }
    },
    defineNameStatus() {
      if (this.item_user.status === "ONLINE") {
        return "Онлайн";
      } else {
        return "Офлайн";
      }
    },
    defineColorStatus() {
      return {
        success: this.item_user.status === "ONLINE",
        danger: this.item_user.status === "OFFLINE",
      };
    },
  },
  methods: {
    deleteItem() {
      this.isModalDeleteOpen = !this.isModalDeleteOpen;
    },
    editItem() {
      this.$emit("editItem", this.item_user);
    },
  },
};
</script>

