<template>
  <div class="device">
    <Header title="Устройства" description="Устройства 'Маяк-01'" />
    <div class="wrap-loader-2" v-if="isLoading">
      <div id="loader"></div>
    </div>
    <div class="container-page">
      <div class="data-tab">
        <div class="data-tab__wrap">
          <div
            class="data-tab__btn"
            @click="TabActiveBlock('block')"
            :class="{ 'active-btn': this.isActiveBlock }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveBlock }"
            >
              Блоки
            </p>
          </div>
          <div
            class="data-tab__btn"
            @click="TabActiveBlock('report')"
            :class="{ 'active-btn': this.isActiveReport }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveReport }"
            >
              Отчеты
            </p>
          </div>
          <div
            class="data-tab__btn"
            @click="TabActiveBlock('statistics')"
            :class="{ 'active-btn': this.isActiveStatistics }"
          >
            <p
              class="data-tab__text"
              :class="{ 'active-text': this.isActiveStatistics }"
            >
              Статистика
            </p>
          </div>
        </div>
      </div>
      <div class="device__search-wrap" v-if="this.isActiveBlock">
        <div class="device__search" v-if="this.isActiveCCID">
          <input
            type="text"
            placeholder="Поиск по CCID"
            class="testing__input number"
            v-model="searchCCID"
          />
          <img
            src="../assets/svg/search.svg"
            alt="search"
            class="device__input-svg"
          />
        </div>
        <div class="device__search" v-if="this.isActiveIMEI">
          <input
            type="text"
            placeholder="Поиск по IMEI"
            class="testing__input number"
            v-model="searchIMEI"
          />
          <img
            src="../assets/svg/search.svg"
            alt="search"
            class="device__input-svg"
          />
        </div>
        <div class="data-tab">
          <div class="device__data-tab">
            <div
              class="data-tab__btn"
              @click="activeBlock('CCID')"
              :class="{ 'active-btn': this.isActiveCCID }"
            >
              <p
                class="data-tab__text"
                :class="{ 'active-text': this.isActiveCCID }"
              >
                Поиск по CCID
              </p>
            </div>
            <div
              class="data-tab__btn"
              @click="activeBlock('IMEI')"
              :class="{ 'active-btn': this.isActiveIMEI }"
            >
              <p
                class="data-tab__text"
                :class="{ 'active-text': this.isActiveIMEI }"
              >
                Поиск по IMEI
              </p>
            </div>
          </div>
        </div>
      </div>
      <div class="device-block__list" v-if="this.isActiveBlock">
        <item-device
          v-for="(device, index) in this.allDevice"
          :key="index"
          :device_item="device"
        />
      </div>
      <div class="testing__report-container" v-if="this.isActiveReport">
        <device-active-report />
      </div>
      <div class="testing__statistics-container" v-if="this.isActiveStatistics">
        <device-active-statistics />
      </div>
    </div>
  </div>
</template>

<script>
import ItemDevice from "../components/Device/ItemDevice.vue";
import Header from "../components/Header.vue";
import { mapState } from "vuex";
import { actionTypesDevice } from "@/store/modules/device";
import DeviceActiveReport from "../components/Device/DeviceActiveReport.vue";
import DeviceActiveStatistics from "../components/Device/DeviceActiveStatistics.vue";
export default {
  components: {
    Header,
    ItemDevice,
    DeviceActiveReport,
    DeviceActiveStatistics,
  },
  name: "AppDevice",
  data() {
    return {
      searchCCID: "",
      searchIMEI: "",
      isActiveIMEI: "",
      isActiveCCID: true,
      isActiveBlock: true,
      isActiveReport: false,
      isActiveStatistics: false,
    };
  },
  computed: {
    ...mapState({
      allDevice: (state) => state.device.allDevice,
      isLoading: (state) => state.device.isLoading,
      token: (state) => state.auth.token,
      infoUser: (state) => state.auth.infoUser,
      isConnected: (state) => state.socket.isConnected,
    }),
    searchCCIDReact() {
      return this.searchCCID;
    },
    searchIMEIReact() {
      return this.searchIMEI;
    },
  },
  watch: {
    searchIMEIReact() {
      let imei = this.searchIMEIReact;
      if (imei !== "") {
        this.$store
          .dispatch(actionTypesDevice.searchIMEI, imei)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      } else {
        this.$store
          .dispatch(actionTypesDevice.getAllDevice)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
    searchCCIDReact() {
      let ccid = this.searchCCIDReact;
      if (ccid !== "") {
        this.$store
          .dispatch(actionTypesDevice.searchCCID, ccid)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      } else {
        this.$store
          .dispatch(actionTypesDevice.getAllDevice)
          .then(() => {})
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
  },
  methods: {
    activeBlock(tab) {
      if (tab === "CCID") {
        this.isActiveCCID = true;
        this.isActiveIMEI = false;
        this.searchCCID = "";
      } else if (tab === "IMEI") {
        this.isActiveCCID = false;
        this.isActiveIMEI = true;
        this.searchIMEI = "";
      }
    },
    //TAB
    TabActiveBlock(tab) {
      if (tab === "block") {
        this.isActiveBlock = true;
        this.isActiveReport = false;
        this.isActiveStatistics = false;
      } else if (tab === "report") {
        this.isActiveBlock = false;
        this.isActiveReport = true;
        this.isActiveStatistics = false;
      } else if (tab === "statistics") {
        this.isActiveBlock = false;
        this.isActiveReport = false;
        this.isActiveStatistics = true;
      }
    },
  },
  mounted() {
    if (
      this.token === "undefined" ||
      this.token === "" ||
      this.infoUser === null
    ) {
      this.$router.push({ name: "Login" });
    }
    //ПОДКЛЮЧЕНИЕ СОКЕТОВ ВРЕМЕННО ОТКЛЮЧЕНО
    // if (this.infoUser.id && !this.isConnected) {
    //   this.$store.dispatch(actionTypesSocket.socketConnect, this.infoUser.id);
    // }
    this.$store
      .dispatch(actionTypesDevice.getAllDevice)
      .then(() => {})
      .catch((er) => {
        console.log("Ошибка", er);
      });
  },
};
</script>

<style></style>
