<template>
  <tr class="tr-info-body">
    <td class="td number">{{ idx + 1 }}</td>
    <td class="td number">{{ item_test.imei }}</td>
    <td class="td">{{ item_test.fio }}</td>
    <td class="td number">{{ item_test.timeTest }}</td>
    <!-- Провека светодиодов на кнопке SOS и доп. функции -->
    <td class="td">
      <div class="block-firm" :class="defineColorTest">
        {{ this.defineTest }}
      </div>
    </td>
  </tr>
</template>

<script>
export default {
  name: "AppTestingReportTable",
  props: {
    item_test: {
      type: Object,
      default() {
        return {};
      },
    },
    idx: {
      type: Number,
      default() {
        return {};
      },
    },
  },
  computed: {
    defineColorTest() {
      return {
        "firm-yes": this.item_test.isPassed,
        "firm-no": !this.item_test.isPassed,
      };
    },
    defineTest() {
      if (this.item_test.isPassed) {
        return "Прошел";
      } else {
        return "Не прошел";
      }
    },
  },
};
</script>

